.App {
  text-align: center;
  width: 100%;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* My Stuff */

/* override browser default */
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

/* use viewport-relative units to cover page fully */
/* body {
  height: 100vh;
  width: 100vw;
} */

.game {
  display: inline-block;
  width: 100%;
  height: 90vh;
}

/* full-sized  container that fills up the page */
div {
  height: 100%;
  width: 100%;
}

.leftBox {
  float: left;
  width: 50%;
  height: 90vh;
  background-color: #f590e9;
  display: flex;
  justify-content: center;
  align-items: center;
}

.rightBox {
  float: right;
  width: 50%;
  height: 90vh;
  background-color: #74cef7;
  display: flex;
  justify-content: center;
  align-items: center;
}

.order {
  /* top: 50%; */
}

.content {
  position: absolute;
  display: flex;
  width:40%;
  height:50%;
  margin: auto;
  top:0;
  bottom:0;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.correctAnim {
  color: green;
}

.wrongAnim {
  color: red;
}

.heart {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 15vh;
  width: 15vh;
  filter: invert(95%) sepia(58%) saturate(2%) hue-rotate(353deg) brightness(105%) contrast(100%);
}

.heartCount {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 30vh;
  width: 30vh;
  padding-bottom: 1vh;
  font-size: calc(20px + 4vmin);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}


#result {
  position: absolute;
  top: 10;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 15vh;
  width: 30vh;
  font-size: calc(10px + 2vmin);
}
